html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

.container {
  width: 1660px;
  margin: 0 auto;
  padding: 0 16px;
}

.search-form {
  width: 100%;
  height: 40px;
  background-color: #355561;
  justify-content: center;
  padding: 5px;
  display: flex;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.search-form input {
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 5px;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.search-form button {
  color: #152c33;
  cursor: pointer;
  background-color: #0587b6;
  border: none;
  border-radius: 5px;
  padding: 0 10px;
  font-weight: 600;
  transition-property: color, background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 0);
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.search-form input:focus {
  outline: none;
}

.search-form button:focus, .search-form button:hover, .load-more:hover, .load-more:focus {
  color: #f0f8ff;
  background-color: #043547;
  outline: none;
}

.load-more {
  color: #152c33;
  cursor: pointer;
  background-color: #0587b6;
  border: none;
  border-radius: 5px;
  margin: 12px auto 0;
  padding: 10px;
  font-weight: 600;
  transition-property: color, background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 0);
  display: block;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.hidden {
  display: none;
}

